<template>
  <div>
    <div class='register'>

      <el-steps class='stepsBar' :active="registerStep" finish-status="success" align-center>
        <el-step :title='$t("Login.RegisterStepEmail")'></el-step>
        <el-step :title='$t("Login.RegisterStepPassword")'></el-step>
        <el-step :title='$t("Login.RegisterStepRealInfo")'></el-step>
        <el-step :title='$t("Login.RegisterStepNickname")'></el-step>
        <el-step :title='$t("Login.RegisterStepComplete")'></el-step>
      </el-steps>

      <div class='stepList'>
        <div class='mainPan'>
          <div class='stepEmail' v-show='registerPan[0]'>
            <el-input class='inputBox' v-model='registerEmail' :placeholder='$t("Login.InputEmail")'></el-input>
            <el-input class='inputBox' v-model='registerEmailCode' :placeholder='$t("Login.InputEmailCode")'></el-input>
            <br>
            <el-button class='button buttonLeft' type='primary'
              @click='sendEmailCode'>{{$t("Login.GetEmailCode")}}</el-button>
            <el-button class='button' type='info'
              @click='window.open("mail.jnu.edu.cn")'>{{$t("Login.OpenEmailPage")}}</el-button>
          </div>

          <div class='stepPassword' v-show='registerPan[1]'>
            <el-input class='inputBox' v-model='registerPassword' :placeholder='$t("Login.InputPassword")'
              show-password></el-input>
            <el-input class='inputBox' v-model='registerPasswordConfirm' :placeholder='$t("Login.InputPasswordConfirm")'
              show-password></el-input>
          </div>

          <div class='stepRealInfo' v-show='registerPan[2]'>
            <el-input class='inputBox' v-model='registerStudentID' :placeholder='$t("Login.InputStudentID")'></el-input>
            <el-input class='inputBox' v-model='registerRealName' :placeholder='$t("Login.InputRealName")'></el-input>
            <br>
            <el-select v-model='registerSchool' :placeholder='$t("Login.SelectSchool")'>
              <el-option value='school_sp' :label='$t("Login.SelectSchool0")'></el-option>
              <el-option value='school_py' :label='$t("Login.SelectSchool1")'></el-option>
              <el-option value='school_zh' :label='$t("Login.SelectSchool2")'></el-option>
              <el-option value='school_sz' :label='$t("Login.SelectSchool3")'></el-option>
            </el-select><br>

            <el-tooltip placement="top">
              <div slot='content' class='realInfoAnswer'>
                {{$t("Login.RealInfoAnswer1")}}<br>{{$t("Login.RealInfoAnswer2")}}<br>{{$t("Login.RealInfoAnswer3")}}
              </div>
              <el-link class='realInfoAsk'>
                <el-icon class='el-icon-question'></el-icon>{{$t("Login.RealInfoAsk")}}
              </el-link>
            </el-tooltip>

            <div class='realInfoWarn'>{{$t('Login.RealInfoWarn')}}</div>
          </div>

          <div class='stepNickname' v-show='registerPan[3]'>
            <el-input class='inputBox' v-model='registerNickname' :placeholder='$t("Login.InputNickname")'></el-input>
          </div>

          <div class='stepPolicy' v-show='registerPan[4]'>
            <comp-policy></comp-policy>
            <el-checkbox class='checkbox' v-model='registerAccept'>{{$t("Login.RegisterPolicy")}}</el-checkbox>
          </div>
        </div>
      </div>

      <div class='panFooter'>
        <div class='stepContinue'>
          <el-button class='button buttonLeft' ref='previousStep' @click='previousStep'
            v-show='registerStep!=0'>{{$t("Login.RegisterStepPrevious")}}</el-button>
          <el-button class='button' ref='nextStep' @click='nextStep'
            v-show='registerStep!=4'>{{$t("Login.RegisterStepNext")}}</el-button>
          <el-button class='button' ref='complete' @click='complete' :disabled='!registerAccept'
            v-show='registerStep==4'>{{$t("Login.RegisterStepEnd")}}</el-button>
        </div>

        <div class='switchToLogin'>
          <el-link type='primary' @click='switchToLogin'>{{$t("Login.GotoLogin")}}<br></el-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import md5 from 'js-md5'
import * as server from '../../../server.js'

import { customAlphabet } from 'nanoid';
const nanoid = customAlphabet('1234567890', 9);

export default {
  name: 'comp-register',
  components: {
    CompPolicy: () => import('./CompPolicy.vue')
  },
  data () {
    return {
      registerStep: 0,
      registerPan: [true, false, false, false, false],
      registerEmail: '',
      registerEmailCode: '',
      registerPassword: '',
      registerPasswordConfirm: '',
      registerStudentID: '',
      registerRealName: '',
      registerSchool: '',
      registerNickname: '',
      registerAccept: false,
      registerStepsChecked: [false, false, false, false]
    }
  },
  methods: {
    nextStep () {
      //检查邮箱、密码、学号之类的是否有非法字符

      let loadingInstance = '';

      switch (this.registerStep) {
        case 0:
          if (this.registerEmail == '' || this.registerEmailCode == '') {
            this.alert(this.$t("Login.RegisterEmailCodeEmpty"), this.$t("System.Error"))
            return;
          }
          this.allowNextStep()
          break;

        case 1:
          if (this.registerPassword.length < 8 || this.registerPassword.length > 16) {
            this.alert(this.$t("Login.RegisterPasswordBad"), this.$t("System.Error"))
            return;
          }
          if (this.registerPassword != this.registerPasswordConfirm) {
            this.alert(this.$t("Login.RegisterPasswordWrong"), this.$t("System.Error"))
            return;
          }
          this.allowNextStep()
          break;

        case 2:
          if (this.registerStudentID < 2015000000 || this.registerStudentID > 2024000000) {
            this.alert(this.$t("Login.RegisterStudentIDBad"), this.$t("System.Error"))
            return;
          }
          if (this.registerRealName.length < 2 || this.registerRealName.length > 8) {
            this.alert(this.$t("Login.RegisterRealNameBad"), this.$t("System.Error"))
            return;
          }
          if (this.registerSchool == '') {
            this.alert(this.$t("Login.RegisterSchoolEmpty"), this.$t("System.Error"))
            return;
          }

          //请求服务器，查看该学号是否已被注册
          loadingInstance = this.loading();
          axios({
            method: 'post',
            url: server.default.register2,
            data: new URLSearchParams({
              'studentid': this.registerStudentID,
              'realname': this.registerRealName,
              'school': this.registerSchool
            })
          }).then((response) => {
            loadingInstance.close();
            if (response.status != 200) {
              this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'), {
                confirmButtonText: this.$t('System.Confirm')
              });
            }
            else {
              if (response.data == 'REGISTER_STUDENTID_TAKEN') {
                this.alert(this.$t('Login.RegisterRealInfoTaken'), this.$t('System.Warning'), {
                  confirmButtonText: this.$t('System.Confirm')
                });
              }
              else if (response.data == 'REGISTER_STUDENTID_OK') {
                this.allowNextStep()
              }
              else {
                this.alert('Unexpected Response!', this.$t('System.Error'), {
                  confirmButtonText: this.$t('System.Confirm')
                });
              }
            }
          })
          break;

        case 3:
          if (this.registerNickname.length < 3 || this.registerNickname.length > 10) {
            this.alert(this.$t("Login.RegisterNicknameBad"), this.$t("System.Warning"))
            return;
          }

          //请求服务器，查看该昵称是否已被注册
          loadingInstance = this.loading();
          axios({
            method: 'post',
            url: server.default.register3,
            data: new URLSearchParams({
              'nickname': this.registerNickname,
            })
          }).then((response) => {
            loadingInstance.close();
            if (response.status != 200) {
              this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'), {
                confirmButtonText: this.$t('System.Confirm')
              });
            }
            else {
              if (response.data == 'REGISTER_USERNAME_TAKEN') {
                this.alert(this.$t('Login.RegisterNickNameTaken'), this.$t('System.Warning'), {
                  confirmButtonText: this.$t('System.Confirm')
                });
              }
              else if (response.data == 'REGISTER_USERNAME_OK') {
                this.allowNextStep()
              }
              else {
                this.alert('Unexpected Response!', this.$t('System.Error'), {
                  confirmButtonText: this.$t('System.Confirm')
                });
              }
            }
          })
          break;

        default:
          break;
      }
    },
    allowNextStep () {
      this.registerStepsChecked[this.registerStep] = true;
      this.registerPan[this.registerStep] = false;
      this.registerStep = this.registerStep + 1;
      this.registerPan[this.registerStep] = true;
    },
    previousStep () {
      this.registerPan[this.registerStep] = false;
      this.registerStep = this.registerStep - 1;
      this.registerPan[this.registerStep] = true;
    },
    sendEmailCode () {
      const emailRegex = /^(\w){1,20}@jnu\.edu\.cn$/;
      if (emailRegex.test(this.registerEmail)) {
        console.log('good email');
      }
      else {
        console.log('bad email');
      }
    },
    complete () {
      if (this.registerStepsChecked.filter(item => item == false).length == 0 && this.registerAccept) {
        let schools = ['school_sp', 'school_py', 'school_zh', 'school_sz'];
        const loadingInstance = this.loading();
        axios({
          method: 'post',
          url: server.default.register4,
          data: new URLSearchParams({
            'userid': nanoid(),
            'email': this.registerEmail,
            'nickname': this.registerNickname,
            'password': md5(this.registerPasswordConfirm),
            'studentid': this.registerStudentID,
            'realname': this.registerRealName,
            'school': schools.indexOf(this.registerSchool)
          })
        }).then((response) => {
          loadingInstance.close();
          if (response.status != 200) {
            this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
            return;
          }
          else {
            if (response.data == 'REGISTER_USERID_TAKEN') {
              this.complete();
            }
            else if (response.data == 'REGISTER_COMPLETE') {
              this.alert(this.$t('Login.RegisterSuccess'), this.$t('System.Info'), {
                confirmButtonText: this.$t('System.Confirm')
              });
              this.switchToLogin();
            }
          }
        })
      }
    },
    switchToLogin () {
      this.$bus.$emit('switchLogin', 'login')
    }
  }
}
</script>

<style scoped>
.register {
  height: 400px;
}
.stepsBar {
  margin-bottom: 20px;
}
.inputBox {
  margin-bottom: 15px;
  width: 70%;
}
.button {
  font-size: 17px;
  margin-top: 15px;
  margin-bottom: 15px;
}
.buttonLeft {
  margin-right: 20px;
}
.realInfoAsk {
  font-size: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
}
.realInfoAnswer {
  text-align: center;
  font-size: 15px;
}
.realInfoWarn {
  font-size: 15px;
  text-decoration: underline;
  font-weight: bold;
}
.checkbox {
  margin-top: 20px;
}
</style>